/**
 * _fonts.scss
 * Custom WooCommerce fonts.
 */
@font-face {
	font-family: 'star';
	src: url('./../../../../fonts/plugins/woocommerce/star.eot');
	src: url('./../../../../fonts/plugins/woocommerce/star.eot?#iefix') format('embedded-opentype'),
		url('./../../../../fonts/plugins/woocommerce/star.woff') format('woff'),
		url('./../../../../fonts/plugins/woocommerce/star.ttf') format('truetype'),
		url('./../../../../fonts/plugins/woocommerce/star.svg#star') format('svg');
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: 'WooCommerce';
	src: url('./../../../../fonts/plugins/woocommerce/WooCommerce.eot');
	src: url('./../../../../fonts/plugins/woocommerce/WooCommerce.woff') format('woff'),
		url('./../../../../fonts/plugins/woocommerce/WooCommerce.ttf') format('truetype');
	font-weight: normal;
	font-style: normal;
}
