.woocommerce ul.products li.first, .woocommerce-page ul.products li.first {
  @include bp-phone-xl-reverse() {
    clear: both;
  }
}

.woocommerce .products {
  .product {
    margin-left: 10px;
    margin-right: 10px;
    width: calc(25% - 20px);
    margin-bottom: 15px;
    margin-top: 15px;
    position: relative;
    float: left;
    display: inline-block;
    @include bp-phone-xl() {
      width: calc(50% - 15px);
      margin-left: 5px;
      margin-right: 5px;
      margin-bottom: 6px;
      margin-top: 6px;
    }

    &:nth-child(odd) {
      @include bp-phone-xl() {
        clear: both;
      }
    }

    //&.first {
    //  clear: both;
    //}

    a {
      &:focus {
        outline: none;
      }
    }

    &.product-style-2 {
      .onsale {
        left: 20px;
        right: initial;
        padding: 3px;
        top: 20px;
        min-width: initial;
        padding-left: 11px;
        padding-right: 11px;
        text-transform: initial;
        background: white;
        color: black;
      }

      .badge {
        left: 20px;
        right: initial;
        padding: 3px;
        top: 20px;
        min-width: initial;
        padding-left: 11px;
        padding-right: 11px;
        text-transform: initial;
        background: white;
        color: black;
      }
    }
  }

  &.columns-9 {
    li {
      &.product {
        width: calc(100% / 9 - 20px);
        @include bp-phone-xl() {
          width: calc(50% - 15px);
          margin-left: 5px;
          margin-right: 5px;
        }
      }
    }
  }

  &.columns-8 {
    li {
      &.product {
        width: calc(100% / 8 - 20px);
        @include bp-phone-xl() {
          width: calc(50% - 15px);
          margin-left: 5px;
          margin-right: 5px;
        }
      }
    }
  }

  &.columns-7 {
    li {
      &.product {
        width: calc(100% / 7 - 20px);
        @include bp-phone-xl() {
          width: calc(50% - 15px);
          margin-left: 5px;
          margin-right: 5px;
        }
      }
    }
  }

  &.columns-6 {
    li {
      &.product {
        width: calc(100% / 6 - 20px);
        @include bp-phone-xl() {
          width: calc(50% - 15px);
          margin-left: 5px;
          margin-right: 5px;
        }
      }
    }
  }

  &.columns-5 {
    li {
      &.product {
        width: calc(100% / 5 - 20px);
        @include bp-phone-xl() {
          width: calc(50% - 15px);
          margin-left: 5px;
          margin-right: 5px;
        }
      }
    }
  }

  &.columns-4 {
    li {
      &.product {
        width: calc(100% / 4 - 20px);
        @include bp-phone-xl-reverse() {
          &:nth-child(4n-7) {
            clear: both;
          }
        }
        @include bp-phone-xl() {
          width: calc(50% - 15px);
          margin-left: 5px;
          margin-right: 5px;
        }
      }
    }
  }

  &.columns-3 {
    li {
      &.product {
        width: calc(100% / 3 - 20px);
        @include bp-phone-xl-reverse() {
          &:nth-child(3n) {
            clear: right;
          }
        }
        @include bp-phone-xl() {
          width: calc(50% - 15px);
          margin-left: 5px;
          margin-right: 5px;
        }
      }
    }
  }

  &.columns-2 {
    li {
      &.product {
        width: calc(100% / 2 - 20px);
        @include bp-phone-xl() {
          width: calc(50% - 15px);
          margin-left: 5px;
          margin-right: 5px;
        }
      }
    }
  }

  &.columns-1 {
    li {
      &.product {
        width: calc(100% / 1 - 20px);
        @include bp-phone-xl() {
          width: 100%;
        }
      }
    }
  }
}

.woocommerce ul.products[class*=columns-] li.product {
  @include bp-phone-xl() {
    margin-bottom: 10px;
    margin-top: 10px;
  }
}

.woocommerce ul.products.slick-slider li.product {
  float: none;
}

.woocommerce span.onsale {
  padding: 0;
}

.product {
  .badge {
    position: absolute;
    text-align: center;
    background-color: #77a464;
    color: #fff;
    z-index: 1;
    border-radius: 5px;
    margin: -10px;
    font-size: 12px;
    padding: 10px;
    right: 0;
    top: 0;
    left: initial;
    text-transform: uppercase;
    min-width: 80px;
    min-height: initial;
    font-weight: 500;
    line-height: 16px;
    @include bp-phone-xl() {
      margin: -5px;
    }
  }
}

.woocommerce span.onsale, .woocommerce ul.products li.product .onsale, .wc-block-grid .wc-block-grid__product .wc-block-grid__product-onsale {
  position: absolute;
  text-align: center;
  background-color: #77a464;
  color: #fff;
  z-index: 1;
  border-radius: 5px;
  margin: -10px;
  font-size: 13px;
  padding: 10px;
  right: 0;
  top: 0;
  left: auto;
  text-transform: uppercase;
  min-width: 80px;
  font-weight: 500;
  line-height: 16px;
  min-height: initial;
  border: none;
  @include bp-phone-xl() {
    padding: 5px;
    min-width: 60px;
    font-size: 12px;
  }
}

.woocommerce ul.products {
  margin-left: -10px;
  width: calc(100% + 20px);
  position: relative;

  @include bp-phone-xl() {
    margin-left: -5px;
  }

  .content-wrapper {
    margin-bottom: 15px;
    @include bp-phone-xl() {
      padding-left: 5px;
      padding-right: 5px;
    }
  }

  .slick-prev:before, .slick-next:before {
    color: black;
    bottom: 30px;
    position: relative;
  }

  .slick-list {
    overflow: hidden;
  }

  .addToCart-loader {
    margin: auto;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
  }

  .product {
    &:nth-child(odd) {
      //margin-right: auto !important;
    }
  }
}

.woocommerce ul.products li.product a img {
  margin-bottom: 0;
}

.woocommerce ul.products li.product .button {
  @extend .btn-primary;
  margin-top: auto;
  min-width: initial;
  text-align: center;
  padding-top: 13px;
  padding-bottom: 13px;
}

.woocommerce ul.products li.product .woocommerce-loop-product__title {
  line-height: 20px;
  font-size: 14px;
  color: #4f4f4f;
  font-weight: 300;
  margin-top: 3px;
  padding-bottom: 5px;
  //min-height: 60px;
  margin-bottom: 5px;
}

.woocommerce ul.products li.product .price {
  color: $color-text;
  font-size: 15px;
  margin-bottom: 0;
  font-weight: 500;
}

.woocommerce ul.products li.product .button {
  width: 100%;
  display: inline-block;

  &:focus {
    outline: none;
  }
}


.woocommerce .products.products-amount-3 {
  .product {
    width: calc(100% / 3);
    @include bp-phone-xl() {
      width: 100%;
    }
  }
}
