.b-product-description {
  display: inline-block;
  width: 100%;
  margin-top: 30px;
  padding-bottom: 20px;
  padding-left: $padding-left-container;
  padding-right: $padding-right-container;


  img.alignright {
    padding-left: 30px;
    @include bp-phone-xl() {
      padding-left: 0;
    }
  }
}

.woocommerce-product-details__short-description {
  .description > ul:first-child {
    margin-top: 5px;
  }
}
