.is-slider-product {
  //opacity: 0;
  &.slick-initialized {
    opacity: 1;
    @include bp-phone-xl() {
      margin-bottom: 0 !important;
    }
  }

  .slick-next {
    @include bp-phone-xl() {
      right: 0;
    }
  }

  .slick-prev {
    @include bp-phone-xl() {
      left: 0;
    }
  }
}

.b-products-plans {
  margin-bottom: 30px;
  margin-left: calc(-50vw + 50%);
  margin-right: calc(-50vw + 50%);
  justify-content: center;
  display: flex;
  flex-wrap: wrap;

  .products {
    display: flex;
    flex-wrap: wrap;
    @include bp-tablet-portrait() {
      width: 100%;
    }
  }

  .wishlist-toggle, .onsale {
    display: none;
  }

  .b-price-details {
    font-size: 11px;
    line-height: 14px;
    text-align: center;
    margin-bottom: 4px;
  }

  .woocommerce .products.products-amount-3 .product {
    width: calc(100% / 3 - 20px);
    @include bp-tablet-portrait() {
      width: 100%;
      margin-left: 0;
      margin-right: 0;
    }
  }
}

.b-products-plans, .wc-block-grid__products {
  li.product, .wc-block-grid__product_plan {
    margin: 20px 10px;
    position: relative;
    float: left;
    display: inline-block;
    border-radius: 5px;
    padding: 20px;
    box-shadow: 0px 0px 13px #e9e9e999;
    background: white;
    margin-top: 30px;
    padding-bottom: 25px;
    flex: initial;
    text-align: left;
    @include bp-tablet-portrait() {
      width: 100%;
    }
    @include bp-phone-xl() {
      margin-left: 0;
      margin-right: 0;
      margin-top: 0;
      margin-bottom: 20px;
    }

    &:nth-child(2) {
      margin-top: 0px;
      @include bp-phone-xl() {
        margin-top: 0;
      }
    }

    ul {
      margin-bottom: 19px;
      margin-top: 19px;
    }

    li {
      font-size: 14px;

      i {
        padding-right: 4px;
        color: #1ac535;
        font-size: 16px;
      }
    }

    .b-header {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    .woocommerce-loop-product__title, .wc-block-grid__product-title {
      text-align: left;
      font-size: 20px !important;
      padding-bottom: 10px !important;
      padding-top: 10px !important;
      font-weight: 600 !important;
      display: inline-block;
      width: initial;
    }

    .button {
      display: block;
    }

    .wp-block-button {
      margin-top: 20px;
    }

    .price {
      text-align: center;
      position: relative;
      top: -1px;
      display: flex;
      flex-direction: column-reverse;

      bdi, .woocommerce-Price-amount {
        display: inline-block;
        width: 100%;
        font-size: 24px;
        line-height: 30px;
      }

      del {
        bottom: -1px;

        bdi {
          display: inline-block;
          width: 100%;
          font-size: 15px;
          line-height: 16px;
          text-decoration: line-through;
          color: $color-text;
          position: relative;
          top: -5px;
        }
      }
    }
  }
}

.has-3-columns {
  .wc-block-grid__products {
    .wc-block-grid__product {
      @include bp-phone-xl-reverse() {
        max-width: calc((100% / 3) - 20px);
      }
    }
  }
}
